// app/projects/[slug]/page.tsx
import Link from "next/link";
import { notFound } from "next/navigation";
import { projects } from "@/lib/projects";

export default async function ProjectDetail({
  params,
}: {
  params: Promise<{ slug: string }>;
}) {
  // In Next 15/16, params is a Promise — unwrap it
  const { slug } = await params;
  console.log({ slug });

  const index = projects.findIndex((p) => p.slug === slug);
  console.log({ index });
  if (index === -1) return notFound();

  const project = projects[index];
  const prevIndex = (index - 1 + projects.length) % projects.length;
  const nextIndex = (index + 1) % projects.length;
  const prevSlug = projects[prevIndex].slug;
  const nextSlug = projects[nextIndex].slug;

  // Pick 3 other projects (rotate list from current index for deterministic variety)
  const others = projects.filter((_, i) => i !== index);
  const rotated = others
    .slice(index % others.length)
    .concat(others.slice(0, index % others.length));
  const recommendations = rotated.slice(0, 3);

  return (
    <section>
      <div className="mx-auto max-w-5xl px-4 py-10">
        <Link href="/projects" className="text-sm underline">
          ← All projects
        </Link>

        <h1 className="mt-2 text-3xl sm:text-4xl font-extrabold leading-tight">
          {project.title}
        </h1>
        {project.note ? (
          <p className="text-black/70 mt-1">{project.note}</p>
        ) : null}

        {/* Media: full width */}
        <div className="mt-6">
          <div className="relative">
            {project.videoUrl ? (
              <div className="aspect-video w-full overflow-hidden rounded-xl border border-black/10">
                <iframe
                  title={project.title}
                  className="w-full h-full"
                  src={project.videoUrl}
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                  allowFullScreen
                />
              </div>
            ) : (project.images || []).length ? (
              <div>
                {(project.images || []).map((_, i) => (
                  <div
                    key={i}
                    className="mb-3 last:mb-0 aspect-video rounded-xl bg-black/5 border border-black/10"
                  />
                ))}
              </div>
            ) : (
              <div className="aspect-video rounded-xl bg-black/5 border border-black/10 flex items-center justify-center text-black/40 text-xs">
                Media placeholder
              </div>
            )}

            {/* Prev/Next arrows */}
            <Link
              href={`/projects/${prevSlug}`}
              aria-label="Previous project"
              className="absolute left-2 top-1/2 -translate-y-1/2 rounded-full border border-black/10 bg-white/90 px-3 py-2 text-sm shadow hover:bg-white"
            >
              ←
            </Link>
            <Link
              href={`/projects/${nextSlug}`}
              aria-label="Next project"
              className="absolute right-2 top-1/2 -translate-y-1/2 rounded-full border border-black/10 bg-white/90 px-3 py-2 text-sm shadow hover:bg-white"
            >
              →
            </Link>
          </div>
        </div>

        {/* Case study block: full width under media */}
        <div className="mt-8">
          <div className="rounded-xl border border-black/10 p-4">
            <div className="uppercase tracking-widest text-xs text-black/60">
              Case study
            </div>
            <div className="mt-3 space-y-3 text-sm">
              <p>
                <span className="font-semibold">Problem:</span>{" "}
                {project.problem}
              </p>
              <p>
                <span className="font-semibold">Approach:</span>{" "}
                {project.description}
              </p>
              <p>
                <span className="font-semibold">Result:</span> {project.result}
              </p>
            </div>
            <div className="mt-4 flex gap-2">
              <Link href="/projects" className="underline text-sm">
                ← Back
              </Link>
              <Link href="/#contact" className="underline text-sm">
                Contact
              </Link>
            </div>
          </div>
        </div>

        {/* Recommendations */}
        <div className="pt-10">
          <h3 className="text-xl font-semibold">More projects</h3>
          <p className="text-black/60 text-sm">Hand-picked for you</p>
          <div className="mt-5 flex flex-col gap-4 sm:flex-row sm:flex-wrap">
            {recommendations.map((c) => (
              <Link
                key={c.slug}
                href={`/projects/${c.slug}`}
                className="w-full sm:flex-1 sm:min-w-[220px] rounded-xl border border-black/10 p-4 hover:bg-black/5"
                aria-label={`Open ${c.title}`}
              >
                <div className="aspect-video rounded-md bg-black/5 border border-black/10 mb-3" />
                <div className="font-medium leading-snug">{c.title}</div>
                {c.note ? (
                  <div className="text-xs text-black/60 mt-1">{c.note}</div>
                ) : null}
              </Link>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}
